.home {
    position: relative;
    min-height:100vh;
    align-items: center;
    justify-content: center;
    display: flex;
}
.intro{
    max-width:540px;
    text-align: center;
    z-index: 1;
}
.home__img{
    margin-bottom: 1.5rem;
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials{
    display: flex;
    column-gap: 1.75rem;
    margin: 1.75rem 6rem;
}

.home__social-link{
    color:var(--titile-color);
    font-size: 1.3rem;
    transition: .3s;
}
.home__social-link:hover{
    color:hsl(43, 100%, 68% )
}

.scroll__down{
    position: absolute;
    bottom: 2.5rem;
    left:0;
    width: 100%;
}
.home__scroll-name{
    font-size: var(--small-font-size);
}
.mouse{
    border: 2px solid var(--text-color);
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    border-radius: 1rem;
    position: relative;
}
@keyframes ani-mouse {
    0%{
        top:29%;
    }
    15%{
        top:50%;
    }
    50%{
        top:50%;
    }
    100%{
        top:29%;
    }
    
}
.wheel{
    background-color: var(--titile-color);
    border-radius: 100%;
    width: 0.25rem;
    height:0.25rem;
    position: absolute;
    top:.5rem;
    left:50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}
.shapes{
    position:absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}
.shape{
    position: absolute;
}
.s1{
    left:2%;
    top: 10%;
}
.s2{
    left:18%;
    top: 30%;
}

.s3{
    left:5%;
    bottom: 30%;
}
.s4{
    left:2%;
    bottom: 10%;
}
.s5{
    left:44%;
    top: 10%;
}
.s7{
    left:36%;
    bottom: 10%;
}
.s7{
    top:20%;
    right:25%;
}
.s8{
    right:24%;
    bottom: 20%;
}


.s9{
    right:2%;
    top: 10%;
}
.s10{
    right:11%;
    top: 45%;
}
.s11{
    right:2%;
    bottom: 10%;
}


@media screen and (max-width: 768px) {
    .home__socials {
        margin: 1rem auto; /* Adjusted margin for social links */
    }

    .scroll__down {
        bottom: 1rem; /* Further reduced bottom spacing */
    }

    .mouse {
        height: 1rem; /* Reduced mouse height */
        width: 0.75rem; /* Adjusted mouse width */
    }

    .wheel {
        top: 0.25rem; /* Adjusted top position */
    }
}
